export default {
	props: {
		item: Object,
		index: Number
	},
	data() {
		return {
			currData: this.item
		}
	},
	methods: {
		// 点击关注
		tapFocus() {
			this.currData.isFocus = true;
			// 官网 API-->界面 --> 交互反馈, 弹出框
			uni.showToast({
			    title: '关注成功',
			});
			this.toParentData()
		},
		// 返回到父级信息
		toParentData () {
			this.$emit('mergeData', this.curData, this.index)
		}
	}
}
